//
import styles from './AuthInput.module.scss'

const AuthInput = ({
  inputLabel,
  type,
  placeholder,
  value,
  onChange,
  errorMessage,
  name,
  onKeyDown,
}) => {
  let nameLengthExceed,
    hasNameValue,
    accountNotExist,
    accountOrPasswordWrong,
    wrongEmail,
    wrongCheckPassword,
    sameAccountExist,
    sameEmailExist,
    accountLengthExceed,
    hasAccountValue

  if (inputLabel === '' && value) {
    if (value.length > 50) {
      nameLengthExceed = true
    }
    if (value !== '') {
      hasNameValue = true
    }
  }
  if (inputLabel === '' && value) {
    if (value.length > 10) {
      accountLengthExceed = true
    }
    if (value !== '') {
      hasAccountValue = true
    }
  }
  if (
    inputLabel === '' &&
    errorMessage === 'AssertionError: '
  ) {
    accountNotExist = true
  }
  if (
    inputLabel === '' &&
    errorMessage === 'AssertionError: '
  ) {
    accountOrPasswordWrong = true
  }
  if (errorMessage) {
    if (
      (inputLabel === '' || inputLabel === '') &&
      errorMessage === 'AssertionError: '
    ) {
      wrongCheckPassword = true
    }
    if (
      inputLabel === 'Email' &&
      errorMessage === 'AssertionError: Email'
    ) {
      wrongEmail = true
    }
    if (
      inputLabel === 'Email' &&
      (errorMessage === 'AssertionError: email ' ||
        errorMessage === 'Error: Email ')
    ) {
      sameEmailExist = true
    }
    if (
      inputLabel === '' &&
      (errorMessage === 'AssertionError: account ' ||
        errorMessage === 'Error: Account !')
    ) {
      sameAccountExist = true
    }
  }
  return (
    <>
      <div className={styles.AuthInputContainer}>
        <div className={styles.InputLabel}>{inputLabel}</div>
        <input
          className={
            nameLengthExceed ||
            accountNotExist ||
            wrongCheckPassword ||
            wrongEmail ||
            sameEmailExist ||
            sameAccountExist ||
            accountLengthExceed ||
            accountOrPasswordWrong
              ? styles.ErrorAuthInput
              : styles.AuthInput
          }
          // Jasmine  || onChange  input 
          type={type || 'text'}
          placeholder={placeholder || ''}
          value={value || ''}
          name={name}
          onChange={name ? onChange : (event) => onChange?.(event.target.value)}
          onKeyDown={onKeyDown}
          autoComplete="off"
        />
        <div className={styles.Tips}>
          {/* //  */}
          {nameLengthExceed && (
            <span className={styles.ErrorMessage}></span>
          )}
          {accountNotExist && (
            <span className={styles.ErrorMessage}></span>
          )}
          {accountOrPasswordWrong && (
            <span className={styles.ErrorMessage}></span>
          )}
          {hasNameValue ? (
            <span className={styles.InputValueLength}>{value.length}/50</span>
          ) : null}
          {accountLengthExceed ? (
            <span className={styles.ErrorMessage}></span>
          ) : null}
          {hasAccountValue ? (
            <span className={styles.InputValueLength}>{value.length}/10</span>
          ) : null}
          {wrongCheckPassword && (
            <span className={styles.ErrorMessage}></span>
          )}
          {wrongEmail && (
            <span className={styles.ErrorMessage}>Email </span>
          )}
          {sameEmailExist && (
            <span className={styles.ErrorMessage}>Email </span>
          )}
          {sameAccountExist && (
            <span className={styles.ErrorMessage}>Account </span>
          )}
        </div>
      </div>
    </>
  )
}

export default AuthInput
